body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  background-color: #fff;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.search-form {
  margin-bottom: 20px;
}

input {
  width: 70%;
  box-sizing: border-box;
  margin-right: 10px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  padding: 10px 20px;
}

button:hover {
  background-color: #45a049;
}

.gallery {
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  display: grid;
}

.photo-card {
  text-align: center;
  border: 1px solid #ddd;
  padding: 10px;
}

img {
  max-width: 100%;
  height: auto;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  margin-top: 20px;
  padding: 10px 20px;
  display: none;
}

.load-more:hover {
  background-color: #2980b9;
}

